import React from 'react'
import ReactTooltip from 'react-tooltip'
export default class HintList extends React.Component {
    state = {
        on: false
    }

    handleClick = () => {
        this.setState({
            on: !this.state.on
        })
    }

    handleMouseOut = () => {
        this.setState ({
            on: false
        })
    }

    handleMouseIn = () => {
        this.setState({
            on: !this.state.on
        })
    }

    render(){

        return (
            <div className="hint-list">
                {
                    this.state.on && (
                        <span className="tooltiptext">{this.props.hint}</span>
                    )
                }
                <span className="badge badge-primary badge-pill mx-1 my-auto card-link" data-for={this.props.identifier}><a className="card-link hint" onClick={this.handleClick} onMouseOut={this.handleMouseOut} onMouseOver={this.handleMouseIn} href={this.props.source}>{this.props.lang}</a></span>
            </div>
        )
    }
}

